<template>
  <section class="form" id="contact-us">
    <div class="container">
      <div class="form__body">
        <div v-if="hours !== null" class="form__texts">
          <h2 class="form__texts-title">
            {{ title }}
          </h2>
          <span class="form__texts-subtitle"> {{ subtitle }}</span>
          <div class="form__texts-detail" v-for="(item, i) in details" :key="i">
            <svg
              :width="!isMobile ? item.icon.width : item.icon.width_mob"
              :height="!isMobile ? item.icon.height : item.icon.height_mob"
              aria-hidden="true"
            >
              <use :xlink:href="item.icon.src"></use>
            </svg>
            <p>{{ item.text }}</p>
          </div>
          <div class="form__texts-marathon">
            <p class="form__texts-marathon-title">{{ marathon }}</p>
            <div class="form__texts-timer">
              <div class="form__time">
                <p>{{ computedValues.days }}</p>
                <b>Дней</b>
              </div>
              <span>:</span>
              <div class="form__time">
                <p>{{ computedValues.hours }}</p>
                <b>Часов</b>
              </div>
              <span>:</span>
              <div class="form__time">
                <p>{{ computedValues.minutes }}</p>
                <b>Минут</b>
              </div>
            </div>
          </div>
        </div>

        <form action="get" id="form-consultation" class="form__request">
          <div class="form__fields-wrapper">
            <div class="form__field">
              <input
                class="form__input"
                :class="{ error: form.name.error }"
                id="name"
                name="email"
                :type="form.name.type"
                size="20"
                v-model="form.name.value"
                placeholder=" "
              />
              <label class="form__label" for="name">{{
                form.name.label
              }}</label>
            </div>
            <div class="form__field">
              <input
                class="form__input"
                :class="{ error: form.email.error }"
                id="email"
                name="email"
                :type="form.email.type"
                size="20"
                v-model="form.email.value"
                placeholder=" "
              />
              <label class="form__label" for="email">{{
                form.email.label
              }}</label>
            </div>
          </div>

          <div class="form__send">
            <button
              type="submit"
              name="submit"
              class="button button--consultation"
              @click.prevent="sendFrom()"
            >
              {{ consultation.button }}
            </button>
            <div class="form__agree-wrapper">
              <input
                class="form__checkbox-input visually-hidden"
                v-model="form.accept.value"
                :class="{ error: form.accept.error }"
                id="id"
                type="checkbox"
                @click="chooseAttribute()"
              />

              <label
                for="id"
                class="form__checkbox"
                v-bind:class="{ checked: form.accept.checked }"
              >
              </label>
              <span
                class="form__checkbox-error"
                v-if="form.accept.error.length"
                v-html="form.accept.message"
              ></span>
              <p class="form__agree" v-html="consultation.agree" ></p>
            </div>
          </div>
        </form>
      </div>
    </div>
    <Message :showMessage="showMessage" />
  </section>
</template>

<script>
import Message from './Message.vue'
export default {
  name: 'Form',
  components: {
    Message,
  },
  data() {
    return {
      showMessage: false,
      title: 'Регистрируйтесь  ',
      subtitle: 'на бесплатный 3-дневный марафон от Advance прямо сейчас',
      endTime: new Date('2021-10-11T00:00:00.000+03:00').getTime(),
      hours: null,
      minutes: null,
      seconds: null,
      days: null,
      marathon: 'до начала марафона',

      mobileBreakpoint: 1024,
      windowWidth: window.innerWidth,
      isMobile: false,

      details: [
        {
          text: '22, 23, 24 июня в 20:00',
          icon: {
            width: '29',
            height: '29',
            width_mob: '20.5',
            height_mob: '20.5',
            src: '#time',
          },
        },
        {
          text: 'Онлайн-участие из любой точки мира',
          icon: {
            width: '27',
            height: '21',
            width_mob: '24',
            height_mob: '24',
            src: '#online',
          },
        },
      ],
      consultation: {
        button: 'Забронировать место',
        agree: 'Принимаю условия политики конфиденциальности',
      },
      form: {
        name: {
          value: '',
          label: 'Ваше имя',
          type: 'text',
          id: 'questions-name',
          error: '',
        },
        email: {
          value: '',
          label: 'E-mail',
          type: 'email',
          error: '',
        },

        accept: {
          value: '',
          error: '',
          checked: false,
          message: 'Примите соглашение об обработке персональных данных',
        },
        default: {},
      },
    }
  },

  methods: {
    formatNumber(number) {
      return number < 10 ? `0${number}` : number
    },
    toggleMobile() {
      if (window.innerWidth <= this.mobileBreakpoint) {
        this.isMobile = true
      } else {
        this.isMobile = false
      }
    },
    handleResize() {
      this.toggleMobile()
    },
    chooseAttribute() {
      this.form.accept.checked = !this.form.accept.checked
    },
    async sendFrom() {
      debugger
      if (!this.$validate(this.form)) {
        return
      }

      const data = new FormData()
      // for (let key in this.form) {
      //   if (typeof this.form[key].value !== 'undefined') {
      //     data.append(key, this.form[key].value)
      //   }
      // }

      // data.append('method', 'request')

      setTimeout(() => {
        // this.form = this.$cloneObj(this.form.default)
        // this.form.default = this.$cloneObj(this.form)
        debugger
        this.showMessage = false
      }, 3000)
      this.showMessage = true

      // const response = await axios.post('/api/', data).catch((error) => {
      //   console.log(error)
      // })

      // if (response.data.success) {
      // this.$store.commit('ReviewSuccessOpen')
      // }
    },
  },
  mounted() {
    setInterval(() => {
      const currentTime = new Date().getTime()
      const distance = this.endTime - currentTime
      this.days = Math.floor(distance / (1000 * 60 * 60 * 24))
      this.hours = Math.floor(
        (distance % (1000 * 60 * 60 * 72)) / (1000 * 60 * 60)
      )
      this.minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
      this.seconds = Math.floor((distance % (1000 * 60)) / 1000)
      if (distance < 0) {
        this.$emit('showCounter')
      }
    }, 1000),
      window.addEventListener('resize', this.handleResize)
    this.toggleMobile()
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize)
    this.toggleMobile()
  },

  computed: {
    computedValues() {
      const { days, hours, minutes, seconds } = this
      return {
        days: this.formatNumber(days),
        hours: this.formatNumber(hours),
        minutes: this.formatNumber(minutes),
        seconds: this.formatNumber(seconds),
      }
    },
  },
}
</script>

<style></style>
