import Vue from 'vue'
import VueTheMask from 'vue-the-mask'
import SlideUpDown from 'vue-slide-up-down'
import Sprite from '../components/Sprite.vue'
import HeaderMain from '../components/HeaderMain.vue'
import Banners from '../components/Banners.vue'
import Program from '../components/Program.vue'
import Request from '../components/Request.vue'

import Helpers from '../lib/Helpers.js'

Vue.use(VueTheMask)
Vue.use(Helpers)
Vue.component('slide-up-down', SlideUpDown)

Vue.config.comments = true
Vue.config.ignoredElements = ['noindex', /^ion-/]

Vue.directive('scroll', {
  inserted: function(el, binding) {
    let f = function(evt) {
      if (binding.value(evt, el)) {
        window.removeEventListener('scroll', f)
      }
    }
    window.addEventListener('scroll', f)
    console.log('scroll')
  },
})

const app = new Vue({
  el: '#app',
  data() {
    return {
      window_top: 0,
      header_top: 0,
      header_height: 60,
      offsetLeft: 0,
      width: null,
      height: null,
      timeout_stick: null,
      bowser: {},
      ym_counter: null,
      recaptcha_key: null,
    }
  },
  watch: {
    window_top: {
      handler(new_v, old_v) {
        let header_top

        if (old_v < new_v && new_v > this.header_height) {
          header_top = -this.header_height
          console.log(this.header_height)
        } else {
          header_top = 0
        }

        if (this.timeout_stick !== null) {
          return
        }

        this.timeout_stick = setTimeout(() => {
          this.header_top = header_top
          this.timeout_stick = null
        }, 0)
      },
      deep: true,
    },
  },
  computed: {},
  created() {},
  mounted() {
    this.addListeners()
  },
  methods: {
    addListeners() {
      window.addEventListener('scroll', this.onScroll)
      window.addEventListener('resize', this.onResize)
      window.onload = () => {
        setTimeout(() => {
          this.getOffsetLeft()
        }, 500)
      }
    },
    onScroll(e) {
      this.window_top = window.pageYOffset
    },
    onResize() {
      this.width = window.innerWidth
      this.height = window.innerHeight
      this.getOffsetLeft()
    },
    getOffsetLeft() {
      try {
        this.offsetLeft =
          document.querySelector('footer .container').offsetLeft +
          document.querySelector('footer .container').offsetWidth
      } catch (error) {}
    },
    reachGoal(goal) {
      if (typeof ym !== 'undefined') {
        ym(this.ym_counter, 'reachGoal', goal)
        console.log(goal)
      }
    },
    getRecaptchaToken(action) {
      return new Promise((resolve, reject) => {
        if (typeof grecaptcha === 'undefined') {
          return resolve(false)
        }
        grecaptcha.ready(() => {
          grecaptcha
            .execute(this.recaptcha_key, {
              action: action,
            })
            .then(
              (token) => {
                return resolve(token)
              },
              (error) => {
                console.log(error)
                return resolve(false)
              }
            )
        })
      })
    },
    async getDataAjax(route = '/', config = {}) {
      const response = await axios.get(route, config).catch(function(error) {
        console.log(error)
        return error.response
      })

      return Promise.resolve(response.data)
    },
  },
  components: {
    Sprite,
    HeaderMain,
    Banners,
    Program,
    Request,
  },
})
