<template>
  <header class="header">
    <div class="container">
      <div class="header__top">
        <div class="header__logo">
          <a :href="'https://advance-club.ru/'" class="header__logo-link">
            <img
              loading="lazy"
              :src="logo"
              alt="Логотип Advance"
              :width="logo.width"
              :height="logo.height"
            />
          </a>
        </div>

        <a :href="'tel:' + phone_href" class="header__phone">
          {{ phone }}
        </a>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      menuShown: false,
      mobileBreakpoint: 1024,
      windowWidth: window.innerWidth,
      isMobile: false,
      logo: 'img/advance.png',
      logo_width: 200,
      logo_height: 111,
      logo_width_mob: 64,
      logo_height_mob: 35,
      phone: '8 (800) 700-53-42',
      phone_href: '8 (800) 700-53-42',
    }
  },
  computed: {},
  mounted() {
    window.addEventListener('resize', this.handleResize)
    this.toggleMobile()
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.handleResize)
    this.toggleMobile()
  },
  methods: {
    toggleMobile() {
      if (window.innerWidth <= this.mobileBreakpoint) {
        this.isMobile = true
      } else {
        this.isMobile = false
      }
    },
    handleResize() {
      this.toggleMobile()
    },
  },
  created() {},
  components: {},
}
</script>
